#header {
    width: 100%;
    height: 40px;
    top: 0;
    left: 0;
    padding: 10px 10px;
    background-color: #353533;
    color: #D7D7D5;
    position: relative;
    vertical-align: middle;
    z-index: 100;
}
#logo {
    display: inline-block;
    font-family: "Dosis", "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
    font-size: 1.5em;
    font-weight: 500;
}
#logo img {
    width: 40px;
    height: 40px;
    margin-right: 10px;
    vertical-align: middle;
}
a {
    text-decoration: none;
    color: #D7D7D5;
}
img {
    border: none;
}
